import React from "react"
import Box from "react-bulma-components/lib/components/box"
import Content from "react-bulma-components/lib/components/content"
import Tag from "react-bulma-components/lib/components/tag"
import NewsletterTitle from "./newsletterTitle"
import Emoji from 'a11y-react-emoji'
import PropTypes from "prop-types"
import Obfuscate from "react-obfuscate";

const markUp = data => ({__html: data})

function NewsletterBox(node) {
  return (
    <Box>
      <Content>
        <NewsletterTitle node={node.node} />
        <hr/>
        <p>{node.node.newsletter.welcome}</p>
        <p>{node.node.newsletter.intro}</p>
        {node.node.newsletter.parts.map((part, index) => (
            <>
              <h4>{index + 1}. {part.title} <Emoji symbol={part.emoji.icon} label={part.emoji.label}/></h4>
              <p>
                {part.text.map(text => (
                  <div dangerouslySetInnerHTML={markUp(text)}/>
                ))}
              </p>
              <Tag.Group className={"text-tags"}>
                {part.tags.map(tag => (
                  <Tag key={tag}>#{tag}</Tag>
                ))}
              </Tag.Group>
            </>
          )
        )}
        <hr/>
        <p className={"outro"}>
          <p dangerouslySetInnerHTML={markUp(node.node.newsletter.outro)}/>
          Bitte gerne weiterempfehlen !<br/>
          <a href="https://ivos-ecotainment-newsletter.info">https://ivos-ecotainment-newsletter.info</a><br/><br/>
          Tracking ist aus!<br/>
          Feedback, Forwards, Fails an <Obfuscate email="hello@ivos-ecotainment-newsletter.info"/><br/><br/>
          You are welcome!
        </p>
      </Content>
    </Box>
  )
}

NewsletterBox.defaultProps = {
  node: {},
}

NewsletterBox.propTypes = {
  node: PropTypes.object,
}

export default NewsletterBox
